@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/**
$mq-620-down: "only screen and (max-width: 620px)";
$mq-620-up: "only screen and (min-width: 620px)";

$mq-760-down: "only screen and (max-width: 759px)";
$mq-760-up: "only screen and (min-width: 760px)";

$mq-900-down: "only screen and (max-width: 899px)";
$mq-900-up: "only screen and (min-width: 900px)";

$mq-1000-down: "only screen and (max-width: 999px)";
$mq-1000-up: "only screen and (min-width: 1000px)";

$mq-1180-up: "only screen and (min-width: 1180px)";

$mq-1400-up: "only screen and (min-width: 1400px)";


/**
$mq-320-down: "only screen and (max-width: 320px)";
$mq-320-up: "only screen and (min-width: 320px)";

$mq-640-down: "only screen and (max-width: 640px)";
$mq-640-up: "only screen and (min-width: 640px)";

$mq-980-down: "only screen and (max-width: 980px)";
$mq-980-up: "only screen and (min-width: 980px)";

$mq-1200-down: "only screen and (max-width: 1200px)";
$mq-1200-up: "only screen and (min-width: 1200px)";

*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  color: #000000;
  font-family: "Montserrat", sans-serif;
  line-height: 1.3;
}

main {
  background-color: #ffffff;
}

.navbar {
  display: flex;
  width: 100%;
  background-color: #373C3F;
  justify-content: space-between;
  position: relative;
  height: 37px;
}
.navbar .logo-img {
  display: none;
}
.navbar .logo__text {
  color: #ffffff;
  margin: 12px 0 12px 19px;
  font-size: 13px;
}
.navbar__menu {
  display: none;
  position: absolute;
  top: 37px;
  right: 0;
  height: fit-content;
  list-style-type: none;
  background-color: #FAFAFA;
}
.navbar__menu .navbar__item {
  width: 100%;
  padding: 0 96px 0 12px;
  border-bottom: 2px solid #E9E9E9;
}
.navbar__menu .navbar__item:hover {
  background-color: #F5F5F5;
}
.navbar__menu .item__link {
  line-height: 30px;
  color: #444444;
  text-decoration: none;
  font-size: 12px;
}
.navbar .show {
  display: unset;
}
.navbar .cancel {
  display: none;
}
.navbar .hamburger {
  width: 37px;
  height: 37px;
  padding: 9px 12px 9px 13px;
  background-color: #202529;
}
@media only screen and (min-width: 640px) {
  .navbar {
    height: fit-content;
  }
  .navbar .logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navbar .logo-img {
    display: none;
  }
  .navbar__menu {
    display: none;
    position: absolute;
    top: 74px;
    right: 0;
    height: fit-content;
    list-style-type: none;
    padding: 2px 0 3px 0;
  }
  .navbar__menu .navbar__item {
    padding: 0 130px 0 27px;
  }
  .navbar__menu .item__link {
    text-decoration: none;
    line-height: 43px;
    font-size: 14px;
  }
  .navbar .hamburger {
    padding: 18px 24px 18px 26px;
    width: 82px;
    height: 74px;
  }
}
@media only screen and (min-width: 979px) {
  .navbar {
    flex-direction: row-reverse;
    height: 97px;
    align-items: center;
    padding: 0 30px;
  }
  .navbar .logo__text {
    display: none;
  }
  .navbar .logo-img {
    display: inherit;
    max-height: 97px;
  }
  .navbar .navbar__menu {
    display: flex;
    position: initial;
    height: auto;
    width: 600px;
    align-items: center;
    justify-content: space-between;
    background: none;
    padding: 0;
  }
  .navbar .navbar__item {
    width: auto;
    padding: unset;
    border: none;
  }
  .navbar .item__link {
    color: #EBEBEB;
    text-transform: uppercase;
    font-size: 15px;
  }
  .navbar .hamburger {
    display: none;
  }
}

.hero {
  background-image: url(../app/images/980px/header-980.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 14px 25px 91px 23px;
  margin-bottom: 4px;
}
.hero__text {
  margin-bottom: 5px;
  font-size: 15px;
  color: #ffffff;
}
.hero__text span {
  display: inline-block;
  width: 100%;
  margin-bottom: 3px;
}
.hero__link {
  font-size: 14px;
  text-decoration: none;
  color: #a6d6e0;
}
@media only screen and (min-width: 640px) {
  .hero {
    padding: 30px 228px 197px 38px;
  }
  .hero__text {
    font-size: 33px;
    margin-bottom: 22px;
  }
  .hero__link {
    font-size: 24px;
  }
}
@media only screen and (min-width: 979px) {
  .hero {
    padding: 64px 389px 260px 30px;
  }
  .hero__text {
    font-size: 50px;
    margin-bottom: 57px;
  }
  .hero__link {
    font-size: 34px;
  }
}

