  .main .menu .evening-menu__header h2, .main .menu .day-menu__header h2 {
    font-size: 26px;
  }
  .main .menu .sub-heading__desc {
    font-size: 19px;
  }
  .main .menu .menu__item {
    margin-bottom: 24px;
  }
  .main .menu .item-name, .main .menu .item-price {
    font-size: 17px;
  }
  .main .menu .item-desc {
    font-size: 13px;
  }
}

/*========= FOOTER SECTION =========================*/
.footer-content__container {
  background-color: #ACD8AD;
  color: #ffffff;
